const {ccclass, property} = cc._decorator;

@ccclass
export default class SystemTools{

    public static format(str:string,...args:any[]):string
    {
        let result = str;
        if(args.length > 0)
        {
            let value;
            for (var key in args) 
            {
                value = args[key];
                if(value != null && value.length != 0)
                {
                    var reg = new RegExp(`\\{${key}\\}`,"g");
                    result = result.replace(reg, value);
                }
            }
        }
        

        return result;
    }
}
